h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

@font-face {
  font-family: Roboto, Arial, Helvetica, sans-serif;
  src: url("Roboto-Regular-webfont.55e67705.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

.container {
  max-width: 1148px;
  margin-inline: auto;
  padding-inline: 20px;
}

@media (width >= 640px) {
  .container {
    padding-inline: 41px;
  }
}

@media (width >= 1024px) {
  .container {
    padding-inline: 53px;
  }
}

.page {
  scroll-behavior: smooth;
}

.page:has(.page__menu:target) {
  overflow: hidden;
}

.page__body {
  background-color: #f2f6fa;
  min-width: 320px;
  margin: 0;
}

.page__menu {
  opacity: 0;
  pointer-events: none;
  transition: all .3s ease-in;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  transform: translateX(-100%);
}

.page__menu:target {
  opacity: 1;
  pointer-events: all;
  transform: translateX(0);
}

.header {
  background-color: #2c2c2c;
  border-bottom-right-radius: 30px;
  border-bottom-left-radius: 30px;
  height: 800px;
  margin-bottom: 95px;
}

@media (width >= 1024px) {
  .header {
    margin-bottom: 0;
  }
}

.header__top {
  justify-content: space-between;
  padding-top: 20px;
  display: flex;
}

@media (width >= 1024px) {
  .header__top {
    padding-top: 52px;
  }
}

.header__bar {
  align-items: center;
  column-gap: 32px;
  display: flex;
}

.header__hire-us {
  letter-spacing: 3px;
  color: #fff;
  text-transform: uppercase;
  height: 30px;
  font-family: Poppins, sans-serif;
  font-size: 13px;
  font-weight: 700;
  line-height: 19px;
  text-decoration: none;
  position: relative;
}

.header__hire-us:after {
  content: "";
  background-color: #2060f6;
  border-radius: 4px;
  width: 97%;
  height: 2px;
  display: block;
  position: absolute;
  bottom: 0;
}

.header__logo-link {
  height: 28px;
}

.header__logo {
  display: flex;
}

.header__strategy-agency {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  column-gap: 20px;
  margin-block: 48px;
  display: grid;
}

@media (width >= 640px) {
  .header__strategy-agency {
    --columns: 6;
    column-gap: 30px;
  }
}

@media (width >= 1024px) {
